.smokey-title {
  display: inline-block;
  -webkit-transform-origin: bottom;
          transform-origin: bottom;
  letter-spacing: 0.2em;
  width: 1em;
  color: rgba(0, 17, 255, 0.308);
  -webkit-text-stroke: 2px;
  -webkit-text-stroke-color: #ff0000;
}
.smokey-title:hover {
  -webkit-animation: lettersFade 5s linear forwards;
          animation: lettersFade 5s linear forwards;
  cursor: pointer;
}
.smokey-title::after {
  content: "";
}
@-webkit-keyframes lettersFade {
  0% {
    -webkit-filter: blur(0);
            filter: blur(0);
    -webkit-transform: translateX(0) translateY(0) rotate(0deg) scale(1);
            transform: translateX(0) translateY(0) rotate(0deg) scale(1);
  }
  50% {
    opacity: 0.5;
    pointer-events: none;
    -webkit-transform: translateX(0) translateY(-150px) rotate(50deg) scale(2);
            transform: translateX(0) translateY(-150px) rotate(50deg) scale(2);
  }
  100% {
    -webkit-filter: blur(60px);
            filter: blur(60px);
    -webkit-transform: translateY(-300px) rotate(90deg) scale(4);
            transform: translateY(-300px) rotate(90deg) scale(4);
  }
}
@keyframes lettersFade {
  0% {
    -webkit-filter: blur(0);
            filter: blur(0);
    -webkit-transform: translateX(0) translateY(0) rotate(0deg) scale(1);
            transform: translateX(0) translateY(0) rotate(0deg) scale(1);
  }
  50% {
    opacity: 0.5;
    pointer-events: none;
    -webkit-transform: translateX(0) translateY(-150px) rotate(50deg) scale(2);
            transform: translateX(0) translateY(-150px) rotate(50deg) scale(2);
  }
  100% {
    -webkit-filter: blur(60px);
            filter: blur(60px);
    -webkit-transform: translateY(-300px) rotate(90deg) scale(4);
            transform: translateY(-300px) rotate(90deg) scale(4);
  }
}

