@-webkit-keyframes smoke {
  0% {
    opacity: 0;
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg);
    -webkit-filter: blur(10px);
            filter: blur(10px);
  }
  100% {
    opacity: 1;
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
    -webkit-filter: blur(0);
            filter: blur(0);
    text-shadow: 0 0 10px #ffc6d0, 0 0 20px #b36969, 0 0 40px #ff6d48, 0 0 80px #ffffffa1,
      0 0 160px #ffbb00, 0 0 200px #f4030377;
  }
}

@keyframes smoke {
  0% {
    opacity: 0;
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg);
    -webkit-filter: blur(10px);
            filter: blur(10px);
  }
  100% {
    opacity: 1;
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
    -webkit-filter: blur(0);
            filter: blur(0);
    text-shadow: 0 0 10px #ffc6d0, 0 0 20px #b36969, 0 0 40px #ff6d48, 0 0 80px #ffffffa1,
      0 0 160px #ffbb00, 0 0 200px #f4030377;
  }
}
